<template>
    <div class="image-container">
      <canvas ref="canvas"></canvas>
    </div>
  </template>
  
  <script>
  export default {
    name: 'ImageWithRectangle',
    props: {
      image: {
        type: String,
        required: true
      },
      predictions: {
        type: Array,
        required: true
      }
    },
    mounted() {
      this.drawImageWithRectangle();
    },
    watch: {
      image() {
        this.drawImageWithRectangle();
      },
      predictions() {
        this.drawImageWithRectangle();
      }
    },
    methods: {
      drawImageWithRectangle() {
        const canvas = this.$refs.canvas;
        const ctx = canvas.getContext('2d');
        const image = new Image();
  
        image.onload = () => {
          canvas.width = image.width;
          canvas.height = image.height;
          ctx.drawImage(image, 0, 0);
          this.predictions.forEach((prediction) => {
            const {x, y, width, height} = prediction;
            ctx.beginPath();
            ctx.rect(x - width / 2, y - height / 2, width, height);
            ctx.strokeStyle = 'red';
            ctx.lineWidth = 2;
            ctx.stroke();
            ctx.fillStyle = 'red';
            ctx.font = '16px Arial';
            ctx.fillText(prediction.class, x - width / 2, y - height / 2 - 5);
          });
          // Draw the rectangle
        };
  
        image.src = this.image;
      }
    }
  };
  </script>
  
  <style scoped>
  .image-container {
    position: relative;
  }
  canvas {
    /* Set any styles you need for the canvas */
    height:416px;
  }
  </style>
  