                    </div>
                </div>
                <div v-if="state == st1DroppedImages" class="inline-flex flex-col w-full bg-white bg-opacity-30 rounded-md h-[200px] overflow-y-auto border border-gray-400">
                    <div class="w-full sticky top-0 bg-white inline-flex justify-between z-10 py-3 px-4">
                        <p class="font-mono text-xs ">{{ images.length }} Images Uploaded </p>
                        <button class="text-xs" @click="clearImages()">Clear Selection</button>
                    </div>
                    <div class="grid-container grid grid-cols-3 sm:grid-cols-4 md:grid-cols-5 lg:grid-cols-6 gap-3 md:gap-6 px-4 pb-4">
                        <div v-for="(image, index) in images" :key="index" class="grid-item bg-contain h-16 w-16 rounded-md rounded-md overflow-hidden border border-gray-200">
                            <img class="h-16" :src="image.src" :alt="`Image ${index}`">
                        </div>
                    </div>
                </div>
                <div v-if="state == st3GotEval" class="inline-flex flex-col w-full ">
                    <div class="inline-flex bg-white bg-opacity-40 flex-col gap-4 w-full p-3 rounded-md border border-gray-200 max-w-full">
                        <div class="w-full flex h-[416px] flex-initial items-center justify-center relative">
