               </div>
               <div class="w-full inline-flex items-center justify-center flex-col gap-3">
                    <input v-model="classNames" class="inline-flex w-full rounded-md border border-gray-400 text-sm px-3 py-2 flex-1 break-words" contenteditable placeholder="Enter classes..."/>
                </div>
