            <div class="flex flex-col gap-4">
               <div v-if="state != st3GotEval"  class="flex flex-row gap-3 items-center font-medium text-[#8315F9]">
                <div class="flex w-8 h-8 border border-[#8315F9] flex-none justify-center items-center rounded-full">
                    <img src="../assets/slider.svg" class="inline-flex w-3" />
                </div>
                <p class="text-base text-gray-900  text-gray-800 font-medium">Set Confidence</p>
               </div>
               <div v-if="state == st3GotEval"  class="flex flex-row gap-3 items-center font-medium text-[#8315F9]">
                <div class="flex w-8 h-8 border border-[#8315F9] flex-none justify-center items-center rounded-full">
                    <img src="../assets/slider.svg" class="inline-flex w-3" />
                </div>
                <p class="text-base text-gray-800 font-medium">Change Confidence</p>
               </div>
               <div class="w-full inline-flex items-center justify-center flex-col gap-3">
                <div class="inline-flex w-full">
                    <div class="w-full items-center justify-center inline-flex flex-row gap-6 px-4">
                        <span class="text-xs font-mono text-gray-700">0%</span>
                        <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
                        <span class="text-xs font-mono text-gray-700">100%</span>
                    </div>
                </div>
               </div>
            </div>
            <div class="w-full justify-center inline-flex items-center">
                <button @click="tryme" class="w-max bg-[#8315F9] px-3 py-2 rounded-md text-white font-medium text-base">Try Me</button>
            </div>
        </div>
