</template>

<script>

const st0Start = 0;
const st1DroppedImages = 1;
const st2WaitingForEval = 2;
const st3GotEval = 3;

import api from '../services/mockapi.js'
import {sleep} from '../helpers/sleep.js'
import ImageWithPredictions from './ImageWithPredictions.vue';

function _statusCounts(jobStatus){
    let countDone = 0;
    let countTotal = 0;
    for(const image of jobStatus){
        for(const model of image.models){
            countTotal++;
            if(model.status === "done"){
                countDone++;
            }
        }
    }
    return {countDone, countTotal};
}

export default {
    name: "Page",
    data() {
        const _data = {
            state: "0",
            images: [],
            classNames: "",
            progress: "",
            selectedImage: null,
            availableModels: ["a", "b", "c"],
            selectedModel: null,
        };
        _data.st0Start = st0Start;
        _data.st1DroppedImages = st1DroppedImages;
        _data.st2WaitingForEval = st2WaitingForEval;
        _data.st3GotEval = st3GotEval;
        return _data;
    },
    computed: {
        imagesWithPredictions(){
            return this.images.filter(image => !!image.models)
        },
        selectedPredictions(){
            const image = this.selectedImage;
            const modelName = this.selectedModel;
            const model = image.models.find(m => m.name === modelName);
            return model.annotations;
        }
    },
    methods: {
        onDragOver() {
            // Necessary to allow the drop event to fire
        },
        onDrop(event) {
            this.images = [];
            // Handle dropped image files
            const files = event.dataTransfer.files;
            Array.from(files).forEach((file) => {
                if (file.type.startsWith('image/')) {
                    const reader = new FileReader();
                    reader.onload = (e) => {
                        this.images.push({ src: e.target.result });
                    };
                    reader.readAsDataURL(file);
                }
            });
            this.state = st1DroppedImages;
        },
        clearImages() {
            this.images = [];
            this.state = st0Start;
        },
        async tryme() {
            this.state = st2WaitingForEval;
            const jobId = "" + parseInt(Math.random() * 1000000000000);
            await this._uploadImages(jobId);
            await this._waitForEval(jobId);
        },
        async _uploadImages(jobId) {
            this.progress = "uploading images... 0%";
            const this1 = this;
            const classesArray = this.classNames.split(",").map((className) => className.trim());
            return new Promise(resolve => {
                const this2 = this;
                let uploadCount = 0;
                const imageCount = this.images.length;
                this.images.forEach((image) => {
                    const this3 = this;
                    api.uploadImage(jobId, image.src, classesArray).then(res => {
                        const this4 = this;
                        console.log(this1, this2, this3, this4);
                        image.id = res.image_id;
                        uploadCount++;
                        if (uploadCount === imageCount) {
                            this.progress = "";
                            resolve();
                        }
                        else {
                            this.progress = `Uploading images... ${parseInt(100 * uploadCount / imageCount)}%`;
                        }
                    });
                });
            });
        },
        async _waitForEval(jobId) {
            this.progress = "Waiting for magic annotations...";
            let allDone = false;
            while (!allDone) {
                const jobStatus = await api.getJobStatus(jobId, this.images.map(i => i.id));
                const { countDone, countTotal } = _statusCounts(jobStatus);
                allDone = countDone === countTotal;
                if (!allDone) {
                    this.progress = `Waiting for magic annotations... ${countDone}/${countTotal}`;
                    await sleep(1000);
                } else {
                    for (const i in jobStatus) {
                        this.images[i].models = jobStatus[i].models;
                    }
                    this.availableModels = jobStatus[0].models.map(m => m.name);
                    this.selectedModel = this.availableModels[0];
                    this.state = st3GotEval;
                }
            }
        },
        selectImage(image){
            this.selectedImage = image;
        }
    },
    components: { ImageWithPredictions }
};
</script>

